import React, { useState } from "react"
import { Modal, Button } from 'react-bootstrap'

const OTPModal = (props) => {
    const { isShow, children, title, lblCancel = 'Cancel', lblConfirm = 'Submit', onConfirm, handleCancel } = props;

    const onSubmit = () => {
        onConfirm('')
    }

    return (
        <Modal show={isShow}>
            <Modal.Header>
                <Modal.Title><div className="text-center">{title}</div></Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {children}
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleCancel}>
                    {lblCancel}
                </Button>
                {typeof onConfirm === 'function' ? <Button variant="primary" onClick={onSubmit}>
                    {lblConfirm}
                </Button> : null}
            </Modal.Footer>
        </Modal>
    )
}

export default OTPModal;