const axios = require('axios');

export const API_SUCCESS='API_SUCCESS';
export const API_ERROR='API_ERROR';

export function ResponseSuccess(res=[]) {
	return {
		type:API_SUCCESS,
		payload:
		{	
			status:'success',
			data:res
		}
	}
}

export function ResponseError(res=[]) {
	return {
		type:API_ERROR,
		payload:
		{
			status:'error',
			data:res
		}
	}
}

export const InsertAPI=(apiURL,inputData,cb)=>
{
	return dispatch=>{				
		axios.post(global.serverURL + apiURL, inputData)
		.then((response) => {
			if(response.status===200)
			{							
				return dispatch(ResponseSuccess(response))
			}
		}).catch(error => {
			console.log(error);
			return dispatch(ResponseError(error))
		});
		
	}
}