import axios from "axios";
import React, { useEffect, useState } from "react";
import { MemoizedOrder } from "./orderlist";
const jwt = require('jsonwebtoken')
import Modalbox from './ModalBox';
import { isMobile } from 'react-device-detect';
import Map from "./drivermap";

const buttonstylefocus = {
    cursor: 'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontWeight: '700',
    border: '1px solid #f4511e',
    color: '#fff',
    background: '#f4511e',
    boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)',
    wordWrap: 'break-word'
};
const buttonstyle = {
    cursor: 'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    color: '#f4511e',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    border: '1px solid #f4511e',
    fontWeight: '700',
    textDecoration: 'none',
    background: 'transparent',
    wordWrap: 'break-word'
};

const DriverOTP = () => {
    const [data, setData] = useState([])
    const [show, setShow] = useState(false)
    const [payments, setPayments] = useState('')
    const [otpStatus, setOtpStatus] = useState('')
    const [expired, setExpired] = useState(2)
    const places1 = [{
        "id": 1,
        "name": "Maduraii",
        "latitude": data.length && data[0].dlatitude,
        "longitude": data.length && data[0].dlongitude
    }];

    const loadDriver = () => {
        const index = window.location.href.indexOf("user/")
        const token = window.location.href.substring(index + 5).replace('/', '')
        axios.get(global.serverURL + "/api/courier/checkuser", {
            params: {
                "token": token
            }
        }).then(result => {
            if (result.data.status === 200) {
                const data = result.data.data
                if (data.isDrop) {
                    setExpired(0)
                } else {
                    const splittoken = data.token.split('.')
                    const decrypted = splittoken.map(m => {
                        const str = m.split('')
                        str.splice(12, 1)
                        return str.join('');
                    }).join('.')
                    const decoded = decode({ token: decrypted })
                    setData([decoded.data.body])
                    let status;
                    if (data.isPickup === 0) {
                        status = 'Pickup'
                    } else if (data.isDrop === 0) {
                        status = 'Drop'
                    }
                    setOtpStatus(status)
                }
            } else {
                setExpired(1)
            }
        }).catch(err => {
            setExpired(1)
            console.error(err)
        })
    }
    useEffect(() => {
        loadDriver()
    }, [])

    const handleClose = (item) => {
        if (item.type === 'online' || item.type === 'driver') {

        } else {
            setShow(!show)
            setOtp('')
        }
    }
    const clickType = async (type, res, mobno) => {
        if (type === 'cancelbooking') {
            let conf = window.confirm('Press "OK" to cancel this booking!');
            if (conf) {
                const item = {
                    "id": data[0].id,
                    "action": "reject"
                }
                const result = await axios.post(global.serverURL + "/api/driver/BookingAction", item)
                if (result.status === 'success') {
                    setExpired(3)
                    window.location.reload()
                }
            }
            else window.location.reload()
        }
    }
    const OrderList = () => <MemoizedOrder data={data} isUser handleClose={handleClose} clickType={clickType} otpStatus={otpStatus} onlinePay={handleClose} driverinfo={handleClose} payments={payments} />
    const [otp, setOtp] = useState('')

    const openCheckout = () => {
        let id1 = data[0].id;
        let options = {
            "key": "rzp_live_1oebfIh4RU8cwP",
            "amount": data[0].NetTotal * 100, // 2000 paise = INR 20, amount in paisa
            "name": "Donkeycargo Pvt Ltd.,",
            "description": "Online Shipping System",
            "image": "./dist/img/DonkeyUser.png",
            "handler": (response) => {
                alert("Transaction completed successfully!");

                axios.post(global.serverURL + "/api/courier/updatePaymode", {
                    "id": id1, "paymode": "online", "refno": response.razorpay_payment_id
                }
                )
                    .then((response) => {
                        loadDriver()
                        return response.data.data;
                    }).catch(error => {
                        console.error(error);
                    });

            },
            "prefill": {
                "name": "Enter name",
                "email": "sample@gmail.com"
            },
            "notes": {
                "address": "Enter address"
            },
            "theme": {
                "color": "#F37254"
            }
        };

        let rzp = new window.Razorpay(options);
        rzp.open();
    }

    const NoBookingBox = (text) => {
        return (<div className="page-content">
            <div className={`form-v4-content justify-content-center p-5 ${isMobile ? 'w-100' : 'w-25'}`}>
                <div className="card card-default p-4 text-center" >
                    {expired === 0 ?
                        <i className={`fa fa-check-circle-o fa-3x text-success`} aria-hidden="true"></i>
                        : <i class="fa fa-exclamation-triangle fa-3x text-danger" aria-hidden="true"></i>}
                    <h3 className="mt-3">
                        {text}
                    </h3>
                </div>
            </div>
        </div>)
    }
    return expired === 0 ? (<>{NoBookingBox('Booking successfully completed!')}</>) :
        expired === 1 ? (<>{NoBookingBox('Booking currently unavailable!')}</>) :
            expired === 3 ? (<>{NoBookingBox('Your booking has been cancelled successfully!')}</>) :
                (
                    <div className="page-content">
                        <div className="form-v4-content">
                            <a class="navbar-brand pl-2 pb-2 m-0" href="/" style={{ fontSize: '24px', color: '#fc983c', background: 'bisque', borderRadius: '5px'}}>
                                <img src="https://donkeycargo.com/images/logo.png" style={{width: '2em', marginTop: '10px'}} />   
                                <span className="ml-2">Donkey Cargo
                                    {/* <span style={{verticalAlign: 'super'}}></span> */}
                                    </span></a>
                            <div className="form-detail">
                                <OrderList />
                            </div>
                        </div>
                        <Modalbox isShow={show} title="OTP Information" handleCancel={handleClose}>
                            <div className="d-flex justify-content-center flex-column align-items-center">
                                <div className="row">
                                    <div className="col-12 text-center mb-2">
                                        Your Drop OTP is <span className="bg-danger">{data.length && data[0].recotp}</span>
                                    </div>
                                </div>
                            </div>
                        </Modalbox>

                        <div className="modal fade" id="mapModal" tabIndex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div className="modal-dialog modal-lg modal-dialog-centered">

                                <div className="modal-content">
                                    <div className="modal-body" style={{ padding: '0.5rem' }}>
                                        <div className="row">
                                            <div className="col-12 text-center mb-2">

                                                <div className="">
                                                    <div className="">

                                                        <Map
                                                            center={{ lat: data.length && data[0].dlatitude, lng: data.length && data[0].dlongitude }}
                                                            zoom={11}
                                                            places={places1}
                                                            height='600px'
                                                        />

                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                        <div className="modal-footer" style={{ padding: '0.5rem' }}>
                                            <button type="button" className="btn btn-primary">Refresh</button>
                                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                        </div>
                                    </div>

                                </div>

                            </div>
                        </div>


                        <div id="myModal2" tabIndex="-1" className="modal fade" role="dialog">
                            <div className="modal-dialog  modal-dialog-centered">

                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h4 className="modal-title">Setup Payment</h4>
                                        <button type="button" className="close" data-dismiss="modal">&times;</button>
                                    </div>
                                    <div className="modal-body">
                                        <div className="row">
                                            <div className="col-12">
                                                <a href="#" style={payments === 'Cash Payment' ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" data-dismiss="modal" onClick={() => setPayments('Cash Payment')}>
                                                    <span style={{ display: 'grid' }}><i className="fa fa-truck fa-flip-horizontal fa-3x"></i>
                                                        Cash</span></a>
                                            </div>

                                            <div className="col-12">
                                                <a href="#" style={payments === 'Online Payment' ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => { setPayments('Online Payment'); openCheckout(); }} data-dismiss="modal">
                                                    <span style={{ display: 'grid' }} data-dismiss="modal"><i className="fa fa-credit-card fa-3x"></i>
                                                        Online Payment</span></a>
                                            </div>

                                        </div>
                                        <div className="modal-footer">
                                            <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>

                        <div className="modal fade" id="driverModal" tabIndex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div className="modal-dialog  modal-dialog-centered">

                                <div className="modal-content">
                                    <div className="modal-body" style={{ padding: '0.5rem' }}>
                                        <div className="row">
                                            <div className="col-12 text-center mb-2">

                                                <div className="card card-primary card-outline">
                                                    <div className="card-body box-profile">
                                                        <div className="">
                                                            <img className="profile-user-img img-fluid img-circle"
                                                                src="dist/img/avatar5.png"
                                                                alt="User profile picture" />
                                                        </div>

                                                        <h3 className="profile-username text-center">Mr. {data.length && data[0].driver_name}</h3>

                                                        <p className="text-muted text-center">Madmoney Driver</p>

                                                        <ul className="list-group list-group-unbordered mb-3 text-left">
                                                            <li className="list-group-item">
                                                                <b>Mobile No.</b> <a className="float-right">{data.length && data[0].Drivermobile}</a>
                                                            </li>
                                                            <li className="list-group-item">
                                                                <b>Vehicle No.</b> <a className="float-right">{data.length && data[0].vehicle_number}</a>
                                                            </li>
                                                            <li className="list-group-item">
                                                                <b>Rating</b>
                                                                <a className="float-right">
                                                                    <div>
                                                                        <i className="fa fa-star"></i>
                                                                        <i className="fa fa-star"></i>
                                                                        <i className="fa fa-star"></i>
                                                                        <i className="fa fa-star-half-o"></i>
                                                                        <i className="fa fa-star-o"></i>
                                                                    </div>
                                                                </a>
                                                            </li>
                                                        </ul>

                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                        <div className="modal-footer" style={{ padding: '0.5rem' }}>
                                            {/* <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#mapModal1">Where's Driver?</button> */}
                                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                        </div>
                                    </div>

                                </div>

                            </div>
                        </div>
                    </div>
                )
}

export default DriverOTP;


export const decode = (req, res) => {
    // We can obtain the session token from the requests cookies, which come with every request
    const token = req.token
    // if the cookie is not set, return an unauthorized error
    if (!token) {
        return 'token failed'
    }
    var payload
    try {
        payload = jwt.verify(token, 'Donkey@2017')
    } catch (e) {
        if (e instanceof jwt.JsonWebTokenError) {
            // if the error thrown is because the JWT is unauthorized, return a 401 error
            return 'token failed'
        }
        // otherwise, return a bad request error
        return 'token failed'
    }
    // res.json({ status: "success", data: payload })
    return { status: "success", data: payload }
}
