import React from 'react';
import { Route, Switch, Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom'

//import Front from './booking/front';
import Myorders from './booking/myorders';
//import Myorders from './useradmin/header';
import Map from './booking/map';
import Category from './booking/category';
import Amountcalc from './booking/amountcalc';
import CourierCalc from './booking/couriercalc';
import Front from './booking/front';
import Helpmap from './booking/helpmap';
import Register from './booking/register';
import BookingConfirm from './booking/bookingconfirm';
import DriverOTP from './booking/driverOTP';
import UserOTP from './booking/userOTP';
//import Myorders from './booking/myorders';
import Logout from './useradmin/logout';


class App1 extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			mobno: '',
			rememberMe: false
		}
	}
	componentDidMount() {
		const rememberMe = localStorage.getItem('rememberMe') === 'true';
		const mobno = localStorage.getItem('user');
		global.mobno = mobno;
		this.setState({ rememberMe, mobno });

		const { history } = this.props;

		history.listen((newLocation, action) => {
			if (action === "PUSH") {
				if (
					newLocation.pathname !== this.currentPathname ||
					newLocation.search !== this.currentSearch
				) {
					// Save new location
					this.currentPathname = newLocation.pathname;
					this.currentSearch = newLocation.search;

					// Clone location object and push it to history
					history.push({
						pathname: newLocation.pathname,
						search: newLocation.search
					});
				}
			} else {
				// Send user back if they try to navigate back
				history.go(1);
			}
		});
	}
	render() {
		const rem = this.state.rememberMe
		//const Reg=React.lazy(()=>{})

		return (
			<Switch>
				{this.state.rememberMe
					? <Route exact path='/' component={Myorders} />
					: <Route exact path='/' component={Register} />}

				<Route exact path='/myorders' component={Myorders} />
				<Route exact path='/mybooking' component={Category} />
				<Route exact path='/bookingconfirm' component={BookingConfirm} />
				<Route path='/driver' component={DriverOTP} />
				<Route path='/user' component={UserOTP} />				

				<Route
					render={function () {
						return (rem ? <Route exact path='/' component={Myorders} /> : <Route exact path='/' component={Register} />)
					}}
				/>
			</Switch>
		)
	}
}

export default withRouter(App1);