import React from 'react';
import Map from './map';
import Myorders from './myorders';

class App1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isCourier: true,
      isParcel: false,
      isRental: false,
      isOrder: false,
      isBack: false,
      currentLocation: {
        lat: 9.9442,
        lng: 78.1562
      }
    }
  }
  componentDidMount() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(
        position => {
          let { latitude, longitude } = position.coords
          this.setState({ currentLocation: { lat: latitude, lng: longitude } })
        }, err => {

        });
    } else {

    }
  }
  clickCourier = () => {
    this.setState({ isCourier: true });
  }
  clickParcel = () => {
    this.setState({ isParcel: true });
  }
  clickRental = () => {
    this.setState({ isRental: true });
  }
  clickType = () => {
    this.setState({ isOrder: true });
  }
  render() {
    let res;
    let { isCourier, isParcel } = this.state;

    if (isCourier) {
      return <Map google={this.props.google} center={this.state.currentLocation} height='100vh' zoom={15} isType='courier'
        mapHeader='Courier' />
    }
    else if (isParcel) {
      return <Map google={this.props.google} center={this.state.currentLocation} height='100vh' zoom={15} isType='parcel'
        mapHeader='Parcel' {...this.props} />
    }
    else if (this.state.isBack || this.state.isOrder) {
      return <Myorders />;
    }
    else
      return (
        <div className="wrapper">
          <div className="">
            <div className="row">
              <div className="col-md-6 col-xs-12 text-center">
                <div className="info-box bg-danger">
                  <span className="info-box-icon" onClick={() => { window.location.replace('/Booking/myorders') }}><i
                    className="fa fa-arrow-left fa-md"></i></span>
                  <div className="info-box-content">
                    <span className="info-box-text" style={{ fontSize: '26px' }}>Services</span>
                    <div className="progress">
                      <div className="progress-bar" style={{ width: '100%' }}></div>
                    </div>
                    <span className="progress-description">
                      Choose your type of Delivery
            </span>
                  </div>
                </div>

                <section name="Courier">
                  <button className="buttonborder draw" onClick={this.clickCourier}>
                    <div className="row">
                      <div className="col-md-3 col-xs-12">
                        <img src="./dist/img/courier.png" style={{ height: '90px', width: '100px', marginTop: '10%' }}
                          alt="" />
                      </div>
                      <div className="col-md-9 col-xs-12">
                        <h1>Courier</h1>
                        <p>Intracity door to door 2-wheeler parcel service and Intercity courier service.</p>
                      </div>
                    </div>
                  </button>
                </section>


                <section name="Parcel">
                  <button className="buttonborder draw notification" onClick={this.clickParcel} disabled>
                    <span className="badge">coming soon!</span>
                    <div className="row">
                      <div className="col-md-3 col-xs-12">
                        <img src="./dist/img/parcel.png" style={{ height: '90px', width: '100px', marginTop: '10%' }}
                          alt="" />

                      </div>
                      <div className="col-md-9 col-xs-12">
                        <h1>Parcel</h1>
                        <p>We deliver your valuable parcels with maximum safety. You can send your parcel with
                  <strong>sharing</strong> mode.
                </p>
                      </div>
                    </div>
                  </button>
                </section>


                <section name="Rental">
                  <button className="buttonborder draw notification" onClick={this.clickRental} disabled>
                    <span className="badge">coming soon!</span>
                    <div className="row">
                      <div className="col-md-3 col-xs-12">
                        <img src="./dist/img/rental.png" style={{ height: '90px', width: '100px', marginTop: '10%' }}
                          alt="" />

                      </div>
                      <div className="col-md-9 col-xs-12">
                        <h1>Rental</h1>
                        <p>We will arrange your parcel rental where you want to drop.</p>
                      </div>
                    </div>
                  </button>
                </section>

              </div>

              <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh', width: '100%' }}>
                <img src="./dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
              </div>
              <aside className="control-sidebar control-sidebar-dark">
              </aside>
            </div>
          </div>
        </div>
      );
  }
}

export default App1;
