import React from "react";
/* global google */


export default class SearchBar extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      value: props?.value || '',
    }
    this.autocompleteInput = React.createRef();
    this.autocomplete = null;
    this.handlePlaceChanged = this.handlePlaceChanged.bind(this);
  }

  loadScript = (url, callback) => {
    let script = document.createElement("script");
    script.type = "text/javascript";

    if (script.readyState) {
      script.onreadystatechange = function () {
        if (script.readyState === "loaded" || script.readyState === "complete") {
          script.onreadystatechange = null;
          callback();
        }
      };
    } else {
      script.onload = () => callback();
    }

    script.src = url;
    document.getElementsByTagName("head")[0].appendChild(script);
  };

  handleScriptLoad = () => {
    this.autocomplete = new window.google.maps.places.Autocomplete(this.autocompleteInput.current,
      { "types": [] });

    this.autocomplete.addListener('place_changed', this.handlePlaceChanged);
  }
  componentDidMount() {
    if (this.props.isFillDefault) {
      this.getCurrentLocation()
    }
    if (!window?.google?.maps) this.loadScript(`https://maps.googleapis.com/maps/api/js?key=AIzaSyAGR9WuYIUqrL7ahalIOeDNNrRKBncOgvY&libraries=places`, () => this.handleScriptLoad());
    else this.handleScriptLoad()
  }
  async updateAddress(lat, lng) {
    var obj = "latlng=" + lat + "," + lng;
    let _fire = fetch('https://maps.googleapis.com/maps/api/geocode/json?' + obj + '&key=AIzaSyAGR9WuYIUqrL7ahalIOeDNNrRKBncOgvY')
    try {
      const resp = await _fire;
      const res = await resp.json();
      let result = {
        place: res.results[0].formatted_address,
        coordinates: res.results[0].geometry.location
      }
      this.setState({ value: result.place });
      this.props.onChange(result)
    }
    catch (error) { }
  }
  getCurrentLocation() {
    navigator.geolocation.getCurrentPosition(async (location) => {
      var obj = "latlng=" + location.coords.latitude + "," + location.coords.longitude;
      let _fire = fetch('https://maps.googleapis.com/maps/api/geocode/json?' + obj + '&key=AIzaSyAGR9WuYIUqrL7ahalIOeDNNrRKBncOgvY')
      try {
        const resp = await _fire;
        const res = await resp.json();
        let result = {
          place: res.results[0].formatted_address,
          coordinates: res.results[0].geometry.location
        }
        this.setState({ value: result.place });
        this.props?.onChange(result)
      }
      catch (error) { }
    });
  }
  handlePlaceChanged() {
    const place = this.autocomplete.getPlace();
    let result = {
      place: place.formatted_address,
      coordinates: place.geometry.location.toJSON()
    }
    this.props.onChange(result)

  }

  componentWillReceiveProps(nextProps, nextContext) {
    if (nextProps.value != this.state.value) {
      this.setState({ value: nextProps.value })
    }
  }

  onChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const voidFun = () => null;
    const { onFocus = voidFun, placeholder = "Search here..", style = {} } = this.props;
    return (
      <input
        onFocus={e => { e.target.select(); onFocus() }}
        ref={this.autocompleteInput}
        id="autocomplete"
        placeholder={placeholder}
        type="text"
        defaultValue={this.state.value}
        value={this.state.value}
        className={'className' in this.props ? "form-control" + this.props.className : "form-control"}
        style={style}
        name="value"
        onChange={this.onChange}
      >
      </input>
    );
  }
}