import React from 'react';
import Nav from './nav';
import { withRouter } from 'react-router-dom'
import { Route, Switch, Link } from 'react-router-dom';
class Header extends React.Component {
	constructor(props) {
		super(props);
		this.onLogout = this.onLogout.bind(this);
		this.onHide = this.onHide.bind(this);
		this.state = {
			isRefresh: false,
			isHide: false,
			userData: {
				name: localStorage.getItem('username'),
				mobile: localStorage.getItem('user')
			}
		}
	}
	onHide = () => {
		this.setState(pr => ({ isHide: !pr.isHide }));
		return true;
	}

	onLogout = (e) => {
		localStorage.setItem('rememberMe', false);
		localStorage.setItem('user', '');
		//window.location.reload();
	}
	componentDidMount () {
		global.mobno = localStorage.getItem('user');
		//alert(localStorage.getItem('user'))
		//this.loadPage();
	}

	LoadOnce () {
		window.location.reload();
	}

	render () {
		const { name, mobile } = this.state.userData
		return (
			<div id="header">
				<div className={this.state.isHide ? "app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header closed-sidebar sidebar-mobile-open" : "app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header"}>

					<Nav visible={{ display: 'block' }} x={this.onHide} y={this.state.isHide} />
					<div className="app-main">
						<div className="app-sidebar sidebar-shadow">
							<div className="app-header__logo">

								<div className="header__pane ml-auto">
									<div>
										<button type="button" className="hamburger close-sidebar-btn hamburger--elastic" data-class="closed-sidebar">
											<span className="hamburger-box">
												<span className="hamburger-inner"></span>
											</span>
										</button>
									</div>
								</div>
							</div>
							<div className="app-header__mobile-menu">
								<div>
									<button type="button" className="hamburger hamburger--elastic mobile-toggle-nav">
										<span className="hamburger-box">
											<span className="hamburger-inner"></span>
										</span>
									</button>
								</div>
							</div>

							<div className="scrollbar-sidebar">
								<div className="app-sidebar__inner">
									<ul className="vertical-nav-menu">
										<li className="mt-3 menu-name">Hi {name}!</li>
										<li>{mobile}</li>
										<li className="app-sidebar__heading">Dashboard</li>
										<li>
											<a href="#" onClick={() => window.location.replace('/')}>
												<i className="metismenu-icon pe-7s-plane"></i>
												Home
											</a>
										</li>
										<li className="app-sidebar__heading">My Bookings</li>
										<li>

											<Link to="/mybooking">
												<i className="metismenu-icon pe-7s-plane"></i>
												New Booking
											</Link>

										</li>
										<li>
											<Link to="/myorders" className="mm-active">
												<i className="metismenu-icon pe-7s-rocket"></i>
												Booking List
											</Link>
										</li>

										<li className="app-sidebar__heading">Settings</li>
										<li>
											<a href="/" onClick={this.onLogout}>
												<i className="metismenu-icon pe-7s-key"></i>
												Logout
											</a>
										</li>

									</ul>
								</div>
							</div>
						</div>

						<div className="app-main__outer">
							<div className="app-main__inner">
								<div className="app-page-title">
									<div className="page-title-wrapper">
										<div className="page-title-heading">
											<div className="page-title-icon">
												<i className="metismenu-icon pe-7s-car">
												</i>
											</div>
											<div>{this.props.panelheader}
												<div className="page-title-subheading">{this.props.panelbody}
												</div>
											</div>
										</div>

									</div>
								</div>
								{this.props.render}
							</div>
						</div>

					</div>
				</div>

				<div className="zoom">
					<div className="tooltip">Hover over me
						<span className="tooltiptext">Tooltip text</span>
					</div>

					<Link to="/mybooking" className="zoom-fab zoom-btn-large" id="zoomBtn">
						<i className="fa fa-pencil-square-o fa-2x" style={{ fontSize: '25px', color: 'white', margin: '20px' }}></i></Link>
					<ul className="zoom-menu">
						<li><a className="zoom-fab zoom-btn-sm zoom-btn-person scale-transition scale-out"><i className="fa fa-user"></i></a></li>
						<li><a className="zoom-fab zoom-btn-sm zoom-btn-doc scale-transition scale-out"><i className="fa fa-book"></i></a></li>
						<li><a className="zoom-fab zoom-btn-sm zoom-btn-tangram scale-transition scale-out"><i className="fa fa-dashboard"></i></a></li>
						<li><a className="zoom-fab zoom-btn-sm zoom-btn-report scale-transition scale-out"><i className="fa fa-edit"></i></a></li>
						<li><a className="zoom-fab zoom-btn-sm zoom-btn-feedback scale-transition scale-out"><i className="fa fa-bell"></i></a></li>
					</ul>
					<div className="zoom-card scale-transition scale-out">
						<ul className="zoom-card-content">
							<li>Content</li>
							<li>Content</li>
							<li>Content</li>
							<li>Content</li>
							<li>Content</li>
						</ul>
					</div>
				</div>

			</div>

		);
	}
}

export default withRouter(Header);



