import axios from "axios";
import React, { useEffect, useState } from "react";
import { MemoizedOrder } from "./orderlist";
import OtpInput from 'react-otp-input';
import Modalbox from './ModalBox';
import Switch from "react-switch";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { isMobile } from 'react-device-detect';
const jwt = require('jsonwebtoken')

const DriverOTP = () => {
    const [data, setData] = useState([])
    const [show, setShow] = useState(false)
    const [error, setError] = useState('')
    const [otpStatus, setOtpStatus] = useState('')
    const [expired, setExpired] = useState(2)

    const loadDriver = () => {
        const index = window.location.href.indexOf("driver/")
        const token = window.location.href.substring(index + 7).replace('/', '')
        axios.get(global.serverURL + "/api/courier/checkstatus", {
            params: {
                "token": token
            }
        }).then(result => {
            if (result.data.status === 200) {
                const data = result.data.data
                if (data.isDrop) {
                    setExpired(0)
                } else {
                    const splittoken = data.token.split('.')
                    const decrypted = splittoken.map(m => {
                        const str = m.split('')
                        str.splice(12, 1)
                        return str.join('');
                    }).join('.')
                    const decoded = decode({ token: decrypted })
                    setData([decoded.data.body])
                    let status;
                    if (data.isPickup === 0) {
                        status = 'Pickup'
                    } else if (data.isDrop === 0) {
                        status = 'Drop'
                    }
                    setOtpStatus(status)
                }
            } else {
                setExpired(1)
            }
        }).catch(err => {
            setExpired(1)
            console.error(err)
        })
    }
    useEffect(() => {
        loadDriver()
    }, [])

    const handleClose = () => {
        setShow(!show)
        setOtp('')
    }
    const clickType = async (type, res, mobno) => {
        if (type === 'cancelbooking') {
            let conf = window.confirm('Press "OK" to cancel this booking!');
            if (conf) {
                const item = {
                    "id": data[0].id,
                    "action": "reject"
                }
                const result = await axios.post(global.serverURL + "/api/driver/BookingAction", item)
                if (result.status === 'success') {
                    setExpired(3)
                    window.location.reload()
                }
            }
            else window.location.reload()
        }
    }
    const OrderList = () => <MemoizedOrder data={data} isDriver handleClose={handleClose} clickType={clickType} otpStatus={otpStatus} />
    const [otp, setOtp] = useState('')
    const [checked, setChecked] = useState(false)
    const changeOTP = (otp) => setOtp(otp)
    const handleChange = (otp) => setChecked(!checked)
    const onConfirm = async () => {
        const otpData = {
            id: data[0].id,
            bookingid: data[0].BookingId,
            otp: otp,
            refno: checked ? 'paid' : ''
        }
        if (otpStatus === 'Pickup') {
            const pickup = await axios.post(global.serverURL + "/api/driver/DriverPickup", otpData)
            if (pickup.data.msg === 'success') {
                loadDriver()
                handleClose()
            } else {
                setError('Invalid OTP!')
                setTimeout(() => {
                    setError('')
                }, 5000);
            }
        } else if (otpStatus === 'Drop') {
            const pickup = await axios.post(global.serverURL + "/api/driver/DriverDrop", otpData)
            if (pickup.data.msg === 'success') {
                loadDriver()
                handleClose()
            } else {
                setError('Invalid OTP!')
                setTimeout(() => {
                    setError('')
                }, 5000);
            }
        }

    }

    const Alert = (props) => {
        return <MuiAlert elevation={6} variant="filled" {...props} severity="error" />;
    }

    const { Name, UserMobileno, RecName, RecMobile, LocalAdd1, LocalAdd2 } = data.length && data[0]
    const NoBookingBox = (text) => {
        return (<div className="page-content">
            <div className={`form-v4-content justify-content-center p-5 ${isMobile ? 'w-100' : 'w-25'}`}>
                <div className="card card-default p-4 text-center" >
                    {expired === 0 ?
                        <i className={`fa fa-check-circle-o fa-3x text-success`} aria-hidden="true"></i>
                        : <i class="fa fa-exclamation-triangle fa-3x text-danger" aria-hidden="true"></i>}
                    <h3 className="mt-3">
                        {text}
                    </h3>
                </div>
            </div>
        </div>)
    }
    return expired === 0 ? (<>{NoBookingBox('Your Booking successfully completed!')}</>) :
        expired === 1 ? (<>{NoBookingBox('Booking currently unavailable!')}</>) :
            expired === 3 ? (<>{NoBookingBox('Your booking has been cancelled successfully!')}</>) :
                (
                    <div className="page-content">
                        <div className="form-v4-content">
                        <a class="navbar-brand pl-2 pb-2 m-0" href="/" style={{ fontSize: '24px', color: '#fc983c', background: 'bisque', borderRadius: '5px'}}>
                                <img src="https://donkeycargo.com/images/logo.png" style={{width: '2em', marginTop: '10px'}} />   
                                <span className="ml-2">Donkey Cargo
                                    {/* <span style={{verticalAlign: 'super'}}></span> */}
                                    </span></a>
                            <div className="form-detail">
                                <OrderList />
                            </div>
                            <form className="form-detail p-1" action="#" method="post" id="myform" noValidate="novalidate">
                                <div className="card card-default px-3 py-4">
                                    <div className="row">
                                        <div className="col-9">
                                            <h3>Pickup Information</h3>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Name</label>
                                            <div className="input-group">
                                                <input type="text" className="form-control" disabled value={Name} />
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Phone</label>
                                            <div className="input-group">
                                                <a href={`tel:${UserMobileno}`} className="w-100">
                                                    <input type="text" className="form-control" disabled value={UserMobileno} />
                                                </a>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                                <div className="card card-default px-3 py-4">
                                    <div className="row">
                                        <div className="col-9">
                                            <h3>Drop Information</h3>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Name</label>
                                            <div className="input-group">
                                                <input type="text" className="form-control" disabled value={RecName} />
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Phone</label>
                                            <div className="input-group">
                                                <a href={`tel:${RecMobile}`} className="w-100">
                                                    <input type="text" className="form-control" disabled value={RecMobile} />
                                                </a>
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Address</label>
                                            <div className="input-group">
                                                <textarea className="form-control" disabled value={LocalAdd1} />
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <label className="select2text">Landmark</label>
                                            <div className="input-group">
                                                <input type="text" className="form-control" disabled value={LocalAdd2} />
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </form>
                        </div>
                        <Modalbox isShow={show} title="Verification Code" onConfirm={onConfirm} handleCancel={handleClose}>
                            <div className="d-flex justify-content-center flex-column align-items-center">
                                <div>
                                    <OtpInput
                                        value={otp}
                                        onChange={changeOTP}
                                        numInputs={4}
                                        separator={<span>&nbsp;</span>}
                                        inputStyle={{ width: '2em', height: '3em' }}
                                    />
                                </div>
                                {otpStatus === 'Pickup' ? <div className="mt-4 d-flex">
                                    <div>
                                        <Switch onChange={handleChange} checked={checked}
                                            onColor="#86d3ff"
                                            onHandleColor="#2693e6"
                                            handleDiameter={30}
                                            uncheckedIcon={false}
                                            checkedIcon={false}
                                            boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                                            activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                                            height={20}
                                            width={48}
                                            className="react-switch"
                                            id="material-switch"
                                        />
                                    </div>
                                    <div className="ml-2">{checked ? 'Paid' : 'Unpaid'}</div>
                                </div> : null}
                            </div>
                        </Modalbox>
                        <Snackbar open={error} autoHideDuration={5000} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
                            <Alert severity="info">
                                {error}
                            </Alert>
                        </Snackbar>
                    </div>
                )
}

export default DriverOTP;


export const decode = (req, res) => {
    // We can obtain the session token from the requests cookies, which come with every request
    const token = req.token
    // if the cookie is not set, return an unauthorized error
    if (!token) {
        return 'token failed'
    }
    var payload
    try {
        payload = jwt.verify(token, 'Donkey@2017')
    } catch (e) {
        if (e instanceof jwt.JsonWebTokenError) {
            // if the error thrown is because the JWT is unauthorized, return a 401 error
            return 'token failed'
        }
        // otherwise, return a bad request error
        return 'token failed'
    }
    // res.json({ status: "success", data: payload })
    return { status: "success", data: payload }
}
