import React from 'react'

class Nav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isHide: false
        }
    }
    onHide1 = () => {
        this.setState(pr => ({ isHide: !pr.isHide }))
    }
    render() {
        return (

            <div className="app-header header-shadow" >
                <div className="app-header__logo">
                    <div style={{ fontSize: '20px', fontWeight: 'bold', fontFamily: '-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue"' }}><span style={{ color: 'white' }}>DONKEY</span><span style={{ color: 'orange' }}> CARGO</span> </div>
                    <div className="header__pane ml-auto">
                        <div>
                            <button type="button" className="hamburger close-sidebar-btn hamburger--elastic" data-class="closed-sidebar" onClick={this.props.x}>
                                <span className="hamburger-box">
                                    <span className="hamburger-inner"></span>
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
                <div className="app-header__mobile-menu">
                    <div>
                        <button type="button" className={this.props.y ? "hamburger hamburger--elastic mobile-toggle-nav is-active" : "hamburger hamburger--elastic mobile-toggle-nav"} onClick={this.props.x}>
                            <span className="hamburger-box">
                                <span className="hamburger-inner"></span>
                            </span>
                        </button>
                    </div>
                </div>
                <div className="app-header__menu">
                    <span>
                        <button type="button" className="btn-icon btn-icon-only btn btn-primary btn-sm mobile-toggle-header-nav" style={{ display: 'none' }}>
                            <span className="btn-icon-wrapper">
                                <i className="fa fa-ellipsis-v fa-w-6"></i>
                            </span>
                        </button>
                    </span>
                </div>
                <div className="app-header__content">
                    <div className="app-header-right">
                        <div className="header-btn-lg pr-0">
                            <div className="widget-content p-0">
                                <div className="widget-content-wrapper">
                                    <div className="widget-content-left">
                                        <div className="btn-group">
                                            <a data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" className="p-0 btn">
                                                <img width="42" className="rounded-circle" src="assets/images/avatars/1.jpg" alt="" />
                                                <i className="fa fa-angle-down ml-2 opacity-8"></i>
                                            </a>
                                            <div tabIndex="-1" role="menu" aria-hidden="true" className="dropdown-menu dropdown-menu-right">
                                                <button type="button" tabIndex="0" className="dropdown-item">User Account</button>
                                                <button type="button" tabIndex="0" className="dropdown-item">Settings</button>
                                                <h6 tabIndex="-1" className="dropdown-header">Header</h6>
                                                <button type="button" tabIndex="0" className="dropdown-item">Actions</button>
                                                <div tabIndex="-1" className="dropdown-divider"></div>
                                                <button type="button" tabIndex="0" className="dropdown-item">Dividers</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>        </div>
                </div>
            </div>

        )
    }
}

export default Nav;