import React from 'react';
import { buttonstyle, buttonstylefocus, styleForm } from './styles'

var dateFormat = require('dateformat');

export function Orders ({ data, isLocal, clickType, onlinePay, driverinfo, isUser=false, isDriver = false, handleClose, otpStatus, payments }) {
  const orders = data && data.map((data, i) => {
    let booktime = dateFormat(data.BookDate, "dddd, mmm dS, yyyy h:MM:ss TT", true);
    let id = data.BookingId
    let driver1 = data
    let usermobno = data.UserMobileno
    let drivermobno = data.Drivermobile
    const viewpickup = <div>View Pickup&nbsp;OTP</div>
    const viewdrop = <div>View Drop&nbsp;OTP</div>

    return (<div key={i} className={`${isDriver ? 'h-100 px-3 py-4' : ''} card card-default`}>
      <div style={{ marginBottom: '10px' }}>
        <div className={!isDriver ? "callout callout-danger" : ''}>
          <div className="row">
            <div className="col-9">
              <h3>Order #{data.BookingId}</h3>
            </div>
            <div className="col-3" >
              {data.Refno === '' ?
                <button className="btn btn-sm btn-danger" onClick={() => onlinePay({ driver1 })} disabled>
                  Unpaid
                </button> :
                <button className="btn btn-sm btn-success" disabled readOnly>Paid</button>
              }
            </div>

            <div className="col-12 mb-2">
              <p>Placed on {booktime}</p>
            </div>

            <div className="col-12">
              <div style={styleForm}>
                <label className="select2text">From</label>
                <div className="input-group">
                  <span className="input-group-text text-success"><i className="fa fa-dot-circle-o"></i></span>
                  <div className="input-group-prepend">
                  </div>
                  <input type="text" className="form-control" disabled value={data.FromAddress} />
                </div>
              </div>
            </div>

            <div className="col-12">
              <div style={styleForm}>
                <label className="select2text">To</label>
                <div className="input-group">
                  <span className="input-group-text text-danger"><i className="fa fa-dot-circle-o"></i></span>
                  <div className="input-group-prepend">
                  </div>
                  <input type="text" className="form-control" disabled value={data.toaddress} />
                </div>
              </div>
            </div>

            <div className="col-7">
              <div style={styleForm}>
                <label className="select2text">Courier Type</label>
                <div className="input-group mb-3">
                  <span className="input-group-text text-primary"><i className="fa fa-envelope-o"></i></span>
                  <div className="input-group-prepend">
                  </div>
                  <input type="text" className="form-control" disabled value={data.Type||data.CourierType} />
                </div>
              </div>
            </div>
            <div className="col-5 mb-4">
              <div style={styleForm}>
                <label className="select2text">Amount</label>
                <div className="input-group mb-3">
                  <span className="input-group-text bg-danger"><i className="fa fa-inr"></i></span>
                  <div className="input-group-prepend">
                  </div>
                  <input type="text" className="form-control" disabled value={data.NetTotal} />
                </div>
              </div>
            </div>

            {!isDriver ?
              (<>
                <div className="col-6 col-md-3 mb-2 text-center">
                  <a href="#" className="orange-back-link orangebtn" data-toggle="modal" data-target="#myModal3"
                    onClick={() => onlinePay({ ...driver1 })}>{!isUser && data.isPickup === 0  ? viewpickup : viewdrop}
                  </a>
                </div>
                <div className="col-6 col-md-3 text-center" disabled={data.Refno === '' ? false : true}>
                  <a href="#" className="orange-back-link orangebtn" data-toggle="modal" data-target="#myModal2"
                    onClick={() => onlinePay({ ...driver1, type:'online', index:i })}>{!payments&&!data.paymentType? 'Setup Payment': data.paymentType || payments}
                  </a>
                </div>
                <div className="col-6 col-md-3 text-center disabled" disabled={isUser|| !(data.isPickup === 0)}>
                  <a href="#" className="orange-back-link orangebtn"
                    onClick={() => clickType('cancelbooking', { id }, { usermobno })}>Cancel Booking</a>
                </div>
                <div className="col-6 col-md-3 text-center" disabled={drivermobno === null ? true : false}>
                  <a href="#" className="orange-back-link orangebtn" data-toggle="modal" data-target="#driverModal"
                    onClick={() => driverinfo({ ...driver1, type:'driver' })}>Driver's Info</a>
                </div>
              </>) :
              (<>
                <div className="col-6 col-md-6 mb-2 text-center">
                  <a href="#" className="orange-back-link orangebtn" data-toggle="modal" data-target="#myModal3"
                    onClick={handleClose}>
                    Enter {otpStatus} OTP
                  </a>
                </div>
                <div className="col-6 col-md-6 text-center" disabled={data.isPickup == 0 ? false : true}>
                  <a href="#" className="orange-back-link orangebtn"
                    onClick={() => clickType('cancelbooking', { id }, { usermobno })}>Cancel Booking</a>
                </div>
              </>)}


          </div>

        </div>

      </div>
    </div>)
  })
  return (
    <div className="h-100">
      {orders}
    </div>
  );
}

export const MemoizedOrder = React.memo(Orders);