import React from 'react';
import Category from './category';
import Register from './register';
import $ from 'jquery';

class App1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isClick: false
    }
  }

  clickBooking = () => {
    this.setState({ isClick: true });
  }

  render() {
    let res;
    const isClick1 = this.state.isClick;

    res = <div className="wrapper">
      <div className="">

        <div className="row">
          <div className="col-md-6 col-xs-12 text-center">
            <img src="dist/img/DonkeyUser.png" style={{
              height: '128px',
              top: '15%',
              width: '128px',
              marginTop: '15%'
            }} alt="" />
            <span style={{
              fontSize: '24px',
              fontFamily: 'fantasy',
              display: 'block',
              background: '-webkit-linear-gradient( #9400A3,#F7853B)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent'
            }}>Do'n'Key Cargo</span>

            <br />
            <br />
            <span style={{
              fontSize: '24px',
              fontFamily: 'fantasy',
              display: 'block'
            }}>Welcome to DonkeyCargo</span>
            <span>We offer Courier, Parcel and Rental Booking. Will handle your treasured items with care throughout any transition. Let's Get Moving...!!</span>
            <br />
            <br />
            <br />  <button className="button" style={{ width: '200px' }} onClick={this.clickBooking}><span>Start Booking</span></button>
          </div>

          <div className="hidemobile col-md-6 col-xs-12" style={{ height: '100vh', width: '100%' }}>
            <img src="dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
          </div>
        </div>
      </div>
    </div>;

    if (isClick1)
      return <Register />
    else
      return (res);
  }
}

export default App1;
 // background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
 //    WebkitBackgroundClip: 'text',
 //    WebkitTextFillColor: 'transparent' 