import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

import thunk from 'redux-thunk'
import { compose, applyMiddleware, createStore, combineReducers } from 'redux'
import { Provider } from 'react-redux'

import insertReducer from './reducers/insert-reducer'

const allReducers = combineReducers({
	insert: insertReducer
});

//const allStoreEnhancer=compose(applyMiddleware(thunk),window.devToolsExtension&&window.devToolsExtension());

const store = createStore(allReducers, { insert: 'inse' });

ReactDOM.render(<Provider store={store}> <App /> </Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
