import React from 'react';
import { Modal, ModalHeader,ModalBody,Button,ModalFooter } from 'reactstrap';

class ItemAddedModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalOpen: this.props.open || false
    }
  };

  render () {
    const faPadding={padding:'3rem',color:'green'}

    return (
      <div>
      <Modal isOpen={this.state.modalOpen} className={this.props.className}>
      <ModalBody>
      <div className="row">      
      <div className="col-12 text-center">      
      <i style={faPadding} className="fa fa-check-circle-o fa-4x"></i>
      </div>

      <div className="col-12 text-center">
      <h1>Thank You!</h1>      
      <h4>For submitting your feedback.</h4>      
      </div>

      </div>
      </ModalBody>
      <ModalFooter className="text-center" style={{display:'block'}} onClick={()=>this.setState({modalOpen:!this.state.modalOpen})}>
      <div className="text-primary"> Close</div>
      </ModalFooter>

      </Modal>
      </div>
      )
    };
  }
  export default ItemAddedModal